body, head {
    margin: 0;
    font-size: 100%;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    background: #fff;
    height: 100%;
}

.banner {
    background: url(../img/banner.jpg) no-repeat center;
    background-size: cover;
    z-index: -1;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;

    .brand-phrase {
        font-weight: bold;
        color: white;
        text-align: center;
        margin: 40vh 0;
    }
}

nav .btn-outline {
    color: white;
    border: 2px solid white;
    border-radius: 4px;

    &:hover {
        color: #246ba0;
        border: 2px solid #246ba0;
    }
}

.text-1 {
    line-height: 0.9;
}
.blink.cursor {
    animation: blinker 1s alternate infinite;
}

@keyframes blinker {
    50% {
        opacity: 0;
    }
}

.what-we-do {
    text-align: center;
    margin-top: 2rem;
    color: #001421;
    font-weight: 700;
}

.services {
    .service {
        padding-top: 2rem;

        &:last-child {
            padding-bottom: 2rem;
        }
    }

    .fa {
        color: #246ba0;
    }
}

footer {
    margin-top: 1rem;
    padding: 1rem;
    background-color: #001421;
    color: white;
    font-size: 0.8rem;

    a {
        text-decoration: none;
        color: white;
    }

    p {
        margin: 0;

        &:last-child {
            margin-top: 1rem;
        }
    }
}
